body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(47,122,63,1)),to(rgba(91,136,170,1))) fixed;
  background: -webkit-linear-gradient(top, rgba(47,122,63,1) 0%,rgba(91,136,170,1) 100%) fixed;
  background: -o-linear-gradient(top, rgba(47,122,63,1) 0%,rgba(91,136,170,1) 100%) fixed;
  background: linear-gradient(to bottom, rgba(47,122,63,1) 0%,rgba(91,136,170,1) 100%) fixed;
}

p, h2, a {
  text-decoration: none;
  color: white;
  padding: 0;
  margin: 0;
  letter-spacing: 1px;
  font-weight: 300;
}



